.skilllist {
  display:flex;
  justify-content: center;
  align-items:flex-start;
  gap: 1em;
  margin-top: 1em;
  margin-bottom: 1em;
}

.skilllist > div {
  width:100%;
  padding:1em;
  color: var(--black);
  background-color: var(--accent);
}

.skilllist h2 {
  background-color: var(--black);
  color: white;
  padding: 0.3em;
  font-size: 1.5em;
}

@media only screen and (max-width: 600px) {
  .skilllist {
    flex-direction: column;
  }
}