
.logo {
	height: 1.25em;
	margin-right: 0.25em;
}

.front {
	display: flex;
	justify-content: center;
	align-items: center;
	overflow-x: hidden;
}

.frontflex {
	display: flex;
	width: 100%;
	gap: 2.5em;
	align-items: flex-end;
	justify-content: center;
}

.navscontainer {
	font-size: 2em;
	margin-left: 5em;
	margin-top: 2.5em;
}

.navscontainer.flipped {
	transform: scaleX(-1);
  margin-left: 0;
  margin-right: 5em;
}

.navscontainer.flipped button > div {
	transform: scaleX(-1);
}

.about h1 {
	background-color: var(--accent);
	color: var(--black);
	padding: 0.25em 2em;
	padding-left: 0.25em;
	margin-bottom: 0em;
	border-top-right-radius: 0.25em;
	display: inline-block;
	display: flex;
}

.about p {
	font-size: 1.5em;
	margin-bottom: 0.25em;
}

.about .desc {
	background-color: var(--blacktranslucent);
	padding: 0.5em 1em;
}


.navs {
	display:flex;
	flex-direction: column-reverse;
	align-items: center;
	transform-origin: 100% 0%;
	transform: skewY(-15deg);
}

.navs:before {
	content: "";
	width: 100%;
	height: 1.5em;
	bottom: -2.5em;
	left: 0;
	position: absolute;
	background-color: var(--blacktranslucent);
	transform-origin: bottom;
	transform: skewX(45deg);
	transition: 0.5s;
}

.navs:hover:before {
	width: calc(100% + 1.5em);
	left: -1.5em;
}


.navs button {
	width: 8em;
	letter-spacing: 0.15em;
	border: none;
	position: relative;
	transition: 0.5s;
	color: var(--black);
	background-color: var(--accent);
	text-transform: uppercase;
	border: solid 1px var(--black);
}

.navs button:hover {
	transform: translateX(-2.5em);
}

.navscontainer button > div {
	padding: 0.25em 0em;
}

.navscontainer:not(.flipped) button:hover > div {
	width: calc(100% + 2.5em);
	padding-right: 2.5em;
}

.navscontainer.flipped button:hover > div {
	width: calc(100% + 2.5em);
	padding-left: 2.5em;
}

.navscontainer button:hover > div:after {
	cursor: pointer;
	content: "";
	width: calc(100% + 2.5em);
	height: 1.5em;
	filter: brightness(0.75);
	position: absolute;
	left: 0px;
	top: -1.5em;
	transform-origin: bottom;
	transform: skewX(45deg);
}


.navs button:before {
	cursor: pointer;
	content: "";
	width: 1.5em;
	height: 100%;
	background-color: inherit;
	filter: brightness(0.25);
	position: absolute;
	left: -1.5em;
	top: 0px;
	transform-origin: right;
	transform: skewY(45deg);
	border: solid 1px var(--black);
}

.navs button:after {
	cursor: pointer;
	content: "";
	width: 100%;
	height: 1.5em;
	background-color: inherit;
	filter: brightness(0.75);
	position: absolute;
	left: 0px;
	top: -1.5em;
	transform-origin: bottom;
	transform: skewX(45deg);
	border: solid 1px var(--black);
}

@media only screen and (max-width: 1000px) {
	.front {
		display: block;
	}

  .frontflex {
		margin-top: 1em;
		gap: 0.5em;
    align-items: flex-end;
    flex-wrap: wrap;
  }

  .about {
  	margin: 1em;
  	order: -1;
  	width: 100%;
  }

  .navscontainer {
    font-size: 5vw;
		margin: 0 !important;
  	margin-top: 1em !important;
  }

  .navs {
  	font-size: 1em;
  }
}
