@import url('https://fonts.googleapis.com/css2?family=VT323&display=swap');

:root {
  --accent: #e7d4b9;
  --black: #0a1e24;
  --blacktranslucent: #0a1e24aa;
  --white: #fffff9;
}

body {
  margin: 0;
  background-color: var(--black);
  color: var(--white);
  font-family: 'VT323', monospace;
}

p, i {
  font-size:1.2em;
}